@import "./../colors.scss";
@import "./../variables.scss";

.modalBackdrop {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  &_freeze {
    pointer-events: none;
  }
  & > div {
    position: relative;
    display: flex;
    justify-content: center;
  }
  &:before {
    content: "";
    flex: 1;
  }
  &:after {
    content: "";
    flex: 3;
  }
}
.modalContainer {
  position: relative;
  min-width: 400px;
  max-width: 580px;
  min-height: 200px;
  display: inline-block;
  border-radius: $custom-radius;

  background-color: white;
  color: white;
  box-sizing: border-box;
}
.modalHeader {
  font-size: 2.5rem;
  display: flex;
  justify-content: flex-start;
  padding: 2rem 2rem 1rem 2rem;
}
.modalContent {
  display: flex;
  padding: 2rem 3rem;
  line-height: 2rem;
  background-color: rgba(0, 0, 0, 0.02);
  border-top: 1px solid;
  border-bottom: 1px solid;
  border-image: linear-gradient(
    to left,
    transparent 0%,
    rgba(255, 255, 255, 0.15) 50%,
    transparent 100%
  );
  border-image-slice: 1;
}
.modalFooter {
  display: flex;
  justify-content: flex-end;
  padding: 1rem 2rem 2rem 2rem;
  & button {
    margin-left: 12px;
  }
}
.modalBtn {
  background-color: white;
  color: black;
  border: none;
  padding: 0.4rem 1rem;
  border-radius: $custom-radius;

  outline: none;
  cursor: pointer;
  &:hover {
    background-color: rgb(235, 235, 235);
  }
}
.modalClose {
  position: absolute;
  padding: 5px;
  right: 18px;
  top: 15px;
  cursor: pointer;
}
